/**
 * System imports
 ============================================================================ */
import React from "react";
import { Alert } from "react-bootstrap";
import { useTranslation } from "react-i18next";

/**
  * User imports
  ============================================================================ */
import Overlay from "../overlay";
import AlertContainer from "../alertContainer";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {};

  /**
   * Properties
   */
  const props = Object.assign(defaults, args.data);

  /**
   * Barrels
   */
  const { error, info } = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  const { onAlertClose: onClose } = props.barrelEventHandlers;

  // let t;
  const errorStrings = useTranslation("Errors").t;
  const infoStrings = useTranslation("Infos").t;

  if (error !== null && info !== null) {
    const msgError = errorStrings(error.msg);
    const msgInfo = infoStrings(info.msg);

    return (
      <>
        <AlertContainer visible={true}>
          <Overlay
            visible={true}
            variant="danger"
            onClose={() => onClose("error")}
            dismissible={error.dismissible}
          >
            {msgError}
          </Overlay>

          <Overlay
            visible={true}
            variant="info"
            onClose={() => onClose("info")}
            dismissible={info.dismissible}
          >
            {msgInfo}
          </Overlay>
        </AlertContainer>
      </>
    );
  } else if (error !== null) {
    const msgError = errorStrings(error.msg);

    return (
      <AlertContainer visible={true}>
        <Overlay
          visible={true}
          variant="danger"
          onClose={() => onClose("error")}
          dismissible={error.dismissible}
        >
          {msgError}
        </Overlay>
      </AlertContainer>
    );
  } else if (info !== null) {
    const msgInfo = infoStrings(info.msg);

    return (
      <AlertContainer visible={true}>
        <Overlay
          visible={true}
          variant="info"
          onClose={() => onClose("info")}
          dismissible={info.dismissible}
        >
          {msgInfo}
        </Overlay>
      </AlertContainer>
    );
  }
  return <></>;
};
